import axios from 'axios';
import Vue from 'vue';

const analysis =  function (action,href) {
	let uri = href?href:window.location.href;
		let round = setInterval(() => {
			if (window.sessionStorage.getItem('tel')) {
				clearInterval(round);
				axios.get(Vue.prototype.VUE_ANALYSIS_API, {
					params: {
						requestid: 'h5_log',
						uid: window.sessionStorage.getItem('tel'),
						action: action,
						count: 1,
						url: uri
					}
				}).then(function (response) {
					console.log(response);
				}).catch(function (error) {
					console.log(error);
				});
			}
		}, 500);
}

export default analysis

